'use client';

import React, { useState } from 'react';
import { useRouter } from 'next/navigation';
import { getToken } from '../utils/auth';

interface CariTemelBilgi {
  CARI_KOD: string;
  CARI_ISIM: string;
  CARI_IL: string;
  CARI_ILCE: string;
  CARI_ADRES: string;
  CARI_TEL?: string;
  EMAIL?: string;
  VERGI_DAIRESI?: string;
  VERGI_NUMARASI?: string;
}

interface CariData {
  CariTemelBilgi: CariTemelBilgi;
  CariEkBilgi: any;
  SubelerdeOrtak: boolean;
  IsletmelerdeOrtak: boolean;
}

interface ArpsResponse {
  TotalCount: number;
  Limit: number;
  Offset: number;
  IsSuccessful: boolean;
  Data: CariData[];
}

interface CariKayitForm {
  CARI_ISIM: string;
  CARI_ADRES: string;
  CARI_TEL: string;
  CARI_IL: string;
  CARI_ILCE: string;
  CARI_TIP: string;
  DOVIZLIMI: string;
  M_KOD: string;
  POSTAKODU: string;
  VERGI_NUMARASI: string;
  VERGI_DAIRESI: string;
}

export default function DashboardPage() {
  const [cariList, setCariList] = useState<CariData[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [showTable, setShowTable] = useState(false);
  const [isTableVisible, setIsTableVisible] = useState(false);
  const [showCariForm, setShowCariForm] = useState(false);
  const [isFormVisible, setIsFormVisible] = useState(false);
  const [formData, setFormData] = useState<CariKayitForm>({
    CARI_ISIM: '',
    CARI_ADRES: '',
    CARI_TEL: '',
    CARI_IL: '',
    CARI_ILCE: '',
    CARI_TIP: 'A',
    DOVIZLIMI: 'H',
    M_KOD: '',
    POSTAKODU: '',
    VERGI_NUMARASI: '',
    VERGI_DAIRESI: ''
  });
  
  const router = useRouter();

  const handleFormSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      setLoading(true);
      setError(null);

      const token = getToken();
      if (!token) {
        router.push('/login');
        return;
      }

      const cariData = {
        CariTemelBilgi: {
          Sube_Kodu: 1,
          ISLETME_KODU: 1,
          CARI_KOD: `Y${Math.floor(Math.random() * 9000) + 1000}`, // Rastgele kod
          ...formData,
          DETAY_KODU: 0,
          NAKLIYE_KATSAYISI: 0.0,
          RISK_SINIRI: 0.0,
          TEMINATI: 0.0,
          CARISK: 0.0,
          CCRISK: 0.0,
          SARISK: 0.0,
          SCRISK: 0.0,
          CM_BORCT: 0.00,
          CM_ALACT: 0.00,
          ISKONTO_ORANI: 0.0,
          VADE_GUNU: 20,
          LISTE_FIATI: 0,
          DOVIZ_TIPI: 0,
          DOVIZ_TURU: 0,
          HESAPTUTMASEKLI: "Y",
          Update_Kodu: "Y",
          LOKALDEPO: 0,
          C_Yedek1: "A",
          C_Yedek2: "E",
          B_Yedek1: 62,
          ODEMETIPI: 0,
          OnayTipi: "A",
          OnayNum: 0,
          MUSTERIBAZIKDV: "H",
          TeslimCariBagliMi: "H",
          ULKE_KODU: "TR"
        }
      };

      const response = await fetch('http://172.16.20.230:7171/api/v2/ARPs', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(cariData)
      });

      if (!response.ok) {
        throw new Error('Cari kayıt edilemedi');
      }

      const result = await response.json();
      if (result.IsSuccessful) {
        setFormData({
          CARI_ISIM: '',
          CARI_ADRES: '',
          CARI_TEL: '',
          CARI_IL: '',
          CARI_ILCE: '',
          CARI_TIP: 'A',
          DOVIZLIMI: 'H',
          M_KOD: '',
          POSTAKODU: '',
          VERGI_NUMARASI: '',
          VERGI_DAIRESI: ''
        });
        setShowCariForm(false);
        setIsFormVisible(false);
        alert('Cari başarıyla kaydedildi!');
      } else {
        throw new Error('Cari kayıt işlemi başarısız');
      }
    } catch (err) {
      console.error('Cari kayıt hatası:', err);
      setError('Cari kayıt edilirken bir hata oluştu');
    } finally {
      setLoading(false);
    }
  };

  const handleCardClick = async () => {
    if (showTable) {
      // Eğer tablo gösteriliyorsa, önce animasyonla gizle
      setIsTableVisible(false);
      // Animasyon bittikten sonra tabloyu kaldır
      setTimeout(() => {
        setShowTable(false);
      }, 500); // 500ms animasyon süresi
      return;
    }

    // Tablo gösterilmiyorsa, veriyi çek
    try {
      setLoading(true);
      setError(null);
      
      const token = getToken();
      if (!token) {
        router.push('/login');
        return;
      }

      const response = await fetch('http://172.16.20.230:7171/api/v2/arps', {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error('Cari listesi alınamadı');
      }

      const data: ArpsResponse = await response.json();
      
      if (data.IsSuccessful && data.Data) {
        setCariList(data.Data);
        setShowTable(true);
        // Kısa bir gecikme ile animasyonu başlat
        setTimeout(() => {
          setIsTableVisible(true);
        }, 50);
      } else {
        setError('Cari listesi alınamadı');
      }
    } catch (err) {
      console.error('Cari listesi yüklenirken hata:', err);
      setError('Cari listesi yüklenirken bir hata oluştu');
    } finally {
      setLoading(false);
    }
  };

  const handleCariFormCard = () => {
    if (showCariForm) {
      setIsFormVisible(false);
      setTimeout(() => {
        setShowCariForm(false);
      }, 500);
    } else {
      setShowCariForm(true);
      setTimeout(() => {
        setIsFormVisible(true);
      }, 50);
    }
  };

  if (loading) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-indigo-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Yükleniyor...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100 p-8">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-3xl font-bold text-gray-900 mb-8">Dashboard</h1>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          {/* Cari Listesi Card */}
          <div 
            onClick={handleCardClick}
            className={`bg-white rounded-lg shadow-md p-6 cursor-pointer transform transition-all duration-200 hover:scale-105 ${showTable ? 'ring-2 ring-indigo-500' : ''}`}
          >
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-xl font-semibold text-gray-800">Cari Listesi</h2>
                <p className="text-gray-500 mt-1">
                  {showTable ? 'Listeyi gizle' : 'Tüm carileri görüntüle'}
                </p>
              </div>
              <div className={`text-indigo-600 transform transition-transform duration-300 ${showTable ? 'rotate-180' : ''}`}>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
              </div>
            </div>
          </div>

          {/* Yeni Cari Kayıt Card */}
          <div 
            onClick={handleCariFormCard}
            className={`bg-white rounded-lg shadow-md p-6 cursor-pointer transform transition-all duration-200 hover:scale-105 ${showCariForm ? 'ring-2 ring-green-500' : ''}`}
          >
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-xl font-semibold text-gray-800">Yeni Cari Kayıt</h2>
                <p className="text-gray-500 mt-1">
                  {showCariForm ? 'Formu gizle' : 'Yeni cari kaydı oluştur'}
                </p>
              </div>
              <div className={`text-green-600 transform transition-transform duration-300 ${showCariForm ? 'rotate-180' : ''}`}>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                </svg>
              </div>
            </div>
          </div>
        </div>

        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative mb-8" role="alert">
            <strong className="font-bold">Hata! </strong>
            <span className="block sm:inline">{error}</span>
          </div>
        )}

        {/* Cari Kayıt Formu */}
        {showCariForm && (
          <div 
            className={`transform transition-all duration-500 ease-in-out mb-8 ${
              isFormVisible 
                ? 'translate-y-0 opacity-100' 
                : 'translate-y-4 opacity-0'
            }`}
          >
            <div className="bg-white shadow-md rounded-lg p-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Yeni Cari Kayıt Formu</h3>
              <form onSubmit={handleFormSubmit} className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {/* Düzenlenebilir Alanlar */}
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700">Cari İsim *</label>
                      <input
                        type="text"
                        required
                        value={formData.CARI_ISIM}
                        onChange={(e) => setFormData({...formData, CARI_ISIM: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700">Adres *</label>
                      <input
                        type="text"
                        required
                        value={formData.CARI_ADRES}
                        onChange={(e) => setFormData({...formData, CARI_ADRES: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700">Telefon *</label>
                      <input
                        type="text"
                        required
                        value={formData.CARI_TEL}
                        onChange={(e) => setFormData({...formData, CARI_TEL: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700">İl *</label>
                      <input
                        type="text"
                        required
                        value={formData.CARI_IL}
                        onChange={(e) => setFormData({...formData, CARI_IL: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                  </div>

                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700">İlçe *</label>
                      <input
                        type="text"
                        required
                        value={formData.CARI_ILCE}
                        onChange={(e) => setFormData({...formData, CARI_ILCE: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700">Vergi Dairesi *</label>
                      <input
                        type="text"
                        required
                        value={formData.VERGI_DAIRESI}
                        onChange={(e) => setFormData({...formData, VERGI_DAIRESI: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700">Vergi Numarası *</label>
                      <input
                        type="text"
                        required
                        value={formData.VERGI_NUMARASI}
                        onChange={(e) => setFormData({...formData, VERGI_NUMARASI: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700">Posta Kodu *</label>
                      <input
                        type="text"
                        required
                        value={formData.POSTAKODU}
                        onChange={(e) => setFormData({...formData, POSTAKODU: e.target.value})}
                        className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                      />
                    </div>
                  </div>
                </div>

                {/* Sabit Değerler */}
                <div className="mt-6 bg-gray-50 p-4 rounded-md">
                  <h4 className="text-sm font-medium text-gray-900 mb-3">Sistem Değerleri (Salt Okunur)</h4>
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
                    <div>
                      <span className="text-gray-500">Şube Kodu:</span>
                      <span className="ml-2 text-gray-900">1</span>
                    </div>
                    <div>
                      <span className="text-gray-500">İşletme Kodu:</span>
                      <span className="ml-2 text-gray-900">1</span>
                    </div>
                    <div>
                      <span className="text-gray-500">Detay Kodu:</span>
                      <span className="ml-2 text-gray-900">0</span>
                    </div>
                    <div>
                      <span className="text-gray-500">Vade Günü:</span>
                      <span className="ml-2 text-gray-900">20</span>
                    </div>
                    <div>
                      <span className="text-gray-500">Ülke Kodu:</span>
                      <span className="ml-2 text-gray-900">TR</span>
                    </div>
                  </div>
                </div>

                <div className="flex justify-end space-x-4">
                  <button
                    type="button"
                    onClick={() => {
                      setIsFormVisible(false);
                      setTimeout(() => setShowCariForm(false), 500);
                    }}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                  >
                    İptal
                  </button>
                  <button
                    type="submit"
                    disabled={loading}
                    className="px-4 py-2 text-sm font-medium text-white bg-indigo-600 border border-transparent rounded-md shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 disabled:bg-indigo-400"
                  >
                    {loading ? 'Kaydediliyor...' : 'Kaydet'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}

        {showTable && (
          <div 
            className={`transform transition-all duration-500 ease-in-out ${
              isTableVisible 
                ? 'translate-y-0 opacity-100' 
                : 'translate-y-4 opacity-0'
            }`}
          >
            <div className="bg-white shadow-md rounded-lg overflow-hidden">
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Cari Kodu
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Cari İsim
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        İl/İlçe
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Adres
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        İletişim
                      </th>
                      <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Vergi Bilgileri
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {cariList.map((cari) => (
                      <tr key={cari.CariTemelBilgi.CARI_KOD} className="hover:bg-gray-50">
                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                          {cari.CariTemelBilgi.CARI_KOD}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          {cari.CariTemelBilgi.CARI_ISIM}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {cari.CariTemelBilgi.CARI_IL} / {cari.CariTemelBilgi.CARI_ILCE}
                        </td>
                        <td className="px-6 py-4 text-sm text-gray-500">
                          {cari.CariTemelBilgi.CARI_ADRES}
                        </td>
                        <td className="px-6 py-4 text-sm text-gray-500">
                          <div>Tel: {cari.CariTemelBilgi.CARI_TEL || '-'}</div>
                          <div>E-posta: {cari.CariTemelBilgi.EMAIL || '-'}</div>
                        </td>
                        <td className="px-6 py-4 text-sm text-gray-500">
                          <div>Vergi Dairesi: {cari.CariTemelBilgi.VERGI_DAIRESI || '-'}</div>
                          <div>Vergi No: {cari.CariTemelBilgi.VERGI_NUMARASI || '-'}</div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
} 